main {
    margin:  50px 100px;
    width:  50%;
    float: left;
}

aside{
    width:  20%;
    margin-top: 50px;
    margin-left: 5%;
    float: left;
}

main .user {
    margin-bottom:  50px;
    background-color: #E6E8EC;
    padding:  20px;
    border-radius: 10px;
}

main .delete-icon {
    float: right;
    color: #293162;
    cursor: pointer;
    transform: scale(2);
}

main .edit-icon {
    float: right;
    margin-right: 25px;
    color: #293162;
    cursor: pointer;
    transform: scale(2);
}

main .delete-icon:hover,
main .edit-icon:hover {
    opacity: 0.5;
}

form input:not([type="checkbox"]), 
form textarea,
form button
{
    width: 90%;
    padding: 15px 5%;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    margin-bottom:  10px;
    border: 0;
    background: #EBEBFF;
    color: #23262F;
    outline: none;
    font-size: 15px;
    border-radius: 10px;
}

form input[type="checkbox"] {
    margin: 10px;
}

form button {
    width: 100%;
    cursor: pointer;
    transition: transform 500ms ease;
}

form button:hover {transform: scale(1.1);}

.user form {
    margin-top:  20px;
}

.user form input,
.user form textarea,
.user form button {
    background: #FBFBFB;
}

.user form button:hover{transform: scale(1.01);}

.vkauth button {

    float: right;
    color: #FFFFFF;
    padding: 15px 5%;
    background-color: #4F4DB1;
    cursor: pointer;
    transform: scale(1);
    border-radius: 10px; 
    font-weight: 300;
    margin-bottom:  10px;
    border: 0;
    font-size: 15px;
    font-family: 'Montserrat', sans-serif;
    transition: transform 500ms ease;

}

.vkauth button :hover{transform: scale(1.05);}